import { getActClass } from './utils';


export default {
  name: 'PressActLaunchDialog',
  options: {
    styleIsolation: 'shared',
  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: false,
      desc: '是否显示动效',
    },
    title: {
      type: String,
      default: '即将拉起微信',
      required: false,
      desc: '标题',
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    closeDialog() {
      this.$emit('update:show', false);
      this.$emit('clickCloseButton');
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
